body {
    font-family: 'Helvetica Neue', Helvetica, Roboto, Arial, sans-serif;
    font-weight: normal;
    line-height: 1.5;
    margin: 0;
    padding: 0;
    color: #0a0a0a;
    background: #fefefe;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

img {
    display: inline-block;
    max-width: 100%;
    height: auto;
    vertical-align: middle;
    -ms-interpolation-mode: bicubic;
}

textarea {
    height: auto;
    min-height: 50px;
    border-radius: 0;
}

select {
    width: 100%;
    border-radius: 0;
}

#map_canvas img,
#map_canvas embed,
#map_canvas object,
.map_canvas img,
.map_canvas embed,
.map_canvas object,
.mqa-display img,
.mqa-display embed,
.mqa-display object {
    max-width: none !important;
}

button {
    line-height: 1;
    padding: 0;
    border: 0;
    border-radius: 0;
    background: transparent;

    -webkit-appearance: none;
       -moz-appearance: none;
}

[data-whatinput='mouse'] button {
    outline: 0;
}

.is-visible {
    display: block !important;
}

.is-hidden {
    display: none !important;
}

